import { ACTION_CHANGE_COUNTER, STATE_COUNTER } from '../constants';

export const changeCounter = number => (dispatch) => {
    return dispatch({
        type: ACTION_CHANGE_COUNTER,
        payload: {
            counter: number,
        },
    });
}

export const nextItem = (id) => (dispatch, getState) => {
    const chatData = getState()[STATE_COUNTER].chatData;
    const allData = getState()[STATE_COUNTER].allData;
    const data = chatData;
    data.push(allData[id]);

    dispatch({
        type: 'nextItem',
        payload: {
            chatData: data,
            activeItem: id,
            nextItem: allData[id].next,
            activeItemData: allData[id],
        },
    });

}

export const resetApp = () => dispatch => {
    dispatch({
        type: 'resetApp',
        payload: {},
    })
}