import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { STATE_COUNTER } from '../../constants';
import { changeCounter, nextItem, resetApp } from '../../actions/HomeActions';
import Home from './Home';

const mapStateToProps = state => {
    const { counter, chatData, activeData } = state[STATE_COUNTER];
    const statex = state[STATE_COUNTER];

    return { counter, chatData, activeData, statex };
}

const mapDispatchToProps = (dispatch) => {
    return {
        changeCounter: bindActionCreators(changeCounter, dispatch),
        nextItem: bindActionCreators(nextItem, dispatch),
        resetApp: bindActionCreators(resetApp, dispatch),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);
