import { ACTION_CHANGE_COUNTER } from '../constants';
import data from '../container/Home/data';

const INITIAL_STATE = {
    counter: 0,
    allData: data,
    chatData: [],
    activeItem: 1,
    nextItem: 1,
    activeItemData: {},
};

export default (state = INITIAL_STATE, action) => {
    const { payload } = action;

    switch(action.type) {
        case ACTION_CHANGE_COUNTER:
            return { ...state, counter: action.payload.counter };
        case 'resetApp':
            return { ...state, chatData: [data[1]], activeItem: 1, nextItem: 1, activeItemData: data[1] };    
        case 'nextItem':
            return { ...state, chatData: payload.chatData, activeItem: payload.activeItem, activeItemData: payload.activeItemData };    
        default:
            return state;
    }
}